.room_container {
    width: 100%;
    height: 100vh;
    display: flex;
  }
  
  .chat_section_container {
    height: 100%;
    width: 20%;
    background-color: white;
    display: flex;
    flex-direction: column;
  }
  
  .chat_label_container {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
  }
  
  .chat_label_paragraph {
    font-weight: 700;
    color: #9ca5ab;
    margin-left: 40px;
    font-size: 16px;
  }
  
  .new_message_container {
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }
  
  .new_message_input {
    border: 1px solid #d1dbe2;
    border-radius: 60px;
    padding: 0 20px;
    height: 60px;
    font-size: 16px;
    width: 80%;
    box-shadow: 2px 20px 5px rgba(0, 82, 201, 0.05);
  }
  
  .new_message_textarea:focus {
    outline: none;
  }
  
  .new_message_button {
    position: absolute;
    right: 10%;
  }
  
  .messages_container {
    width: calc(100% - 70px);
    margin-left: 30px;
    margin-right: 30px;
    flex-grow: 1;
    overflow-x: hidden;
    overflow-y: scroll;
  }
  
  .message_container {
    margin-top: 5px;
    display: flex;
    flex-direction: column;
  }
  
  .message_align_left {
    align-items: flex-start;
  }
  
  .message_align_right {
    align-items: flex-end;
  }
  
  .message_left_styles {
    background: rgba(0, 82, 201, 0.1);
    color: black;
  }
  
  .message_right_styles {
    background: linear-gradient(168.68deg, #adecc0 1.12%, #69c6f8 100%);
    color: white;
  }
  
  .message_title {
    margin: 0 0 10px 0;
    padding: 0 10px;
    color: black;
    font-size: 16px;
    font-weight: 500;
    display: block;
  }
  
  .message_content {
    margin: 0px 10px;
    padding: 10px 30px 10px 20px;
    border-radius: 25px;
    font-size: 16px;
    display: inline-block;
  }
  
  /* width */
  ::-webkit-scrollbar {
    width: 5px;
    background: rgba(0, 188, 164, 0.1);
  }
  
  /* Track */
  ::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px grey;
    border-radius: 10px;
  }
  
  /* Handle */
  ::-webkit-scrollbar-thumb {
    background: #e5e5e5;
    border-radius: 10px;
  }
  
  .overlay_container {
    width: 100%;
    height: 100vh;
    background: linear-gradient(168.68deg, #adecc0 1.12%, #69c6f8 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    z-index: 2;
  }
  
  .loader {
    border: 16px solid #f3f3f3; /* Light grey */
    border-top: 16px solid #7aeca0; /* Blue */
    border-radius: 50%;
    width: 80px;
    height: 80px;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  
  .participants_container {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    align-content: flex-start;
    height: 50%;
  }
  
  .direct_chat_container {
    height: 45%;
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100%;
  }
  
  .direct_chat_header {
    height: 50px;
    background: linear-gradient(168.68deg, #adecc0 1.12%, #69c6f8 100%);
    width: 95%;
    border-top-left-radius: 25px;
    border-top-right-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .direct_chat_header_paragraph {
    color: white;
    font-size: 16px;
  }
  
  .new_message_direct_border {
    border-left: 1px solid #e5e5e5;
    border-right: 1px solid #e5e5e5;
    width: 95%;
  }
  
  .direct_messages_container {
    flex-grow: 1;
    width: 95%;
    border-left: 1px solid #e5e5e5;
    border-right: 1px solid #e5e5e5;
  }
  
  .participants_paragraph {
    color: black;
    text-align: center;
    background: #ffffff;
    border-radius: 10px;
    font-weight: 500;
    margin-left: 40px;
    transition: 0.5s;
    margin: 0 40px;
    padding: 10px 0px;
  }
  
  .participants_paragraph:hover {
    background-color: #e5e5e5;
    border-radius: 8px;
  }
  
  .participants_separator_line {
    width: calc(100% - 80px);
    height: 2px;
    background-color: null;
    margin-left: 40px;
    margin-top: 5px;
    margin-bottom: 5px;
  }
  
  .participants_section_container {
    height: 100%;
    width: 20%;
    background-color: white;
    display: flex;
    flex-direction: column;
  }
  
  .participants_label_container {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
  }
  
  .participants_label_paragraph {
      text-align: center;
    font-weight: 700;
    color: #000000;
    margin-left: 80px;
    font-size: 16px;
  }
  
  .local_screen_share_preview {
    position: absolute;
    width: 15%;
    height: 90%;
    background-color: black;
    border-radius: 8px;
    border: 2px solid blue;
    left: 1%;
    bottom: 1%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .video_buttons_container {
    width: 60%;
    height: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 20%;
    bottom: 0;
    background: linear-gradient(168.68deg, #adecc0 1.12%, #69c6f8 100%);
    border-radius: 50px;
    margin-bottom: 10px;
  }
  
  .video_button_container {
    margin-left: 25px;
    display: flex;
    flex-direction: column;
  }
  
  .video_button_image {
    height: 80%;
    padding: 10px;
    border-radius: 25px;
    transition: 0.3s;
  }
  
  .video_button_image:hover {
    background: rgb(0, 0, 0);
    padding: 10px;
    border-radius: 25px;
  }
  
  .video_button_end {
    width: 200px;
    height: 60px;
    font-size: 18px;
    font-weight: 600;
    border-radius: 65px;
    border: none;
    color: white;
    background: #fc5d5b;
    box-shadow: 0px 3px 30px rgba(252, 93, 91, 0.1);
    transition: 0.2s;
  }
  
  .video_button_end:hover {
    background: red;
  }
  
  .videos_container {
    height: 90%;
    width: 100%;
    background-color: #f6f9fb;
    position: relative;
    border-radius: 0px 0px 25px 25px;
  }
  
  .participants {
    display: flex;
    width: 100%;
    height: 100%;
  }
  
  .participant {
    flex-grow: 1;
  }
  
